import React from 'react'
import { Link } from 'react-router-dom'

const ArticlesList = ({ articles }) => (
  <div className="d-flex">
    {articles.map((article, key) => (
      <Link key={key} to={`/article/${article.name}`} style={{ textDecoration: 'none' }}>
        <div className="card p-4 me-4 shadow rounded" style={{width: '18rem'}}>
          <h4 className="mb-4 text-dark">{article.title}</h4>
          <p className="text-dark">{article.content[0].substring(0, 150)}...</p>
        </div>
      </Link>
    ))}
  </div>
)

export default ArticlesList;